<template>
  <div class="home">
    <router-view></router-view>
    <van-tabbar route>
      <van-tabbar-item replace to="/home/index" icon="home-o">
        
      </van-tabbar-item>
      <van-tabbar-item replace to="/home/cate" icon="search">
        
      </van-tabbar-item>
      <van-tabbar-item replace to="/home/cart" icon="cart-o">
        
      </van-tabbar-item>
      <van-tabbar-item replace to="/home/users" icon="manager-o">
        
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>

export default {
  name: "home",
 
  data() {
    return {};
  },
  // - this
  created() {},
  // - DOM
  mounted() {}
};
</script>
<style scoped>
/* @import url(); css */
.van-tabbar{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
}
</style>



